h1, h2, h3, h4, h5, h6, p, ul {
  margin: 0;
  padding: 0;
}

img {
  display: block;
}

button {
  cursor: pointer;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

body {
  background-color: var(--main-bg-color);
  color: var(--main-text-color);
  font-family: Montserrat, sans-serif;
  font-size: 16px;
}

.container {
  max-width: 428px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media only screen and (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}

@media only screen and (min-width: 1200px) {
  .container {
    max-width: 1158px;
  }
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.section-title {
  text-align: center;
  padding-top: 90px;
  padding-bottom: 90px;
}

:root {
  --main-bg-color: #fff;
  --main-text-color: #2e2f42;
  --main-nav-color: #4d5ae5;
  --main-hover-color: #404bbf;
  --main-p-color: #434455;
  --main-p-bottom-color: #f4f4fd;
  --top-line-color: #e7e9fc;
  --icon-hover-green: #31d0aa;
  --customers-border: #8e8f99;
  --transition: 250ms cubic-bezier(.4, 0, .2, 1);
  --input-color: rgba(46, 47, 66, .2);
}

.nav-links {
  letter-spacing: .02em;
  color: var(--main-text-color);
  transition: color var(--transition);
  align-items: center;
  padding: 24px 0;
  font-weight: 500;
  line-height: 1.5;
  text-decoration: none;
  display: flex;
}

.nav-links-contacts {
  letter-spacing: .02em;
  color: var(--main-p-color);
  transition: color var(--transition);
  padding: 24px 0;
  font-style: normal;
  line-height: 1.5;
  text-decoration: none;
}

.contacts {
  flex-direction: column;
  justify-content: center;
  gap: 12px;
  margin-left: auto;
}

@media only screen and (min-width: 1200px) {
  .contacts {
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    gap: 12px;
  }
}

.nav-links-contacts:hover, .nav-links-contacts:focus {
  color: var(--main-hover-color);
}

.menu-nav {
  display: flex;
}

.navigation {
  display: none;
}

.navigation li {
  margin-right: 40px;
}

.navigation li:last-child {
  margin-right: 0;
}

.header {
  z-index: 100;
  background-color: var(--main-bg-color);
  width: 100%;
  border-bottom: 1px solid var(--top-line-color);
  position: fixed;
  top: 0;
  left: 0;
  box-shadow: 0 2px 1px rgba(46, 47, 66, .08), 0 1px 1px rgba(46, 47, 66, .16), 0 1px 6px rgba(46, 47, 66, .08);
}

.nav-links-mobile {
  letter-spacing: .02em;
  color: var(--main-text-color);
  font-size: 36px;
  font-weight: 700;
  line-height: 1.11;
  text-decoration: none;
}

.nav-links-contacts-phone {
  color: var(--main-p-color);
  letter-spacing: .02em;
  font-size: 36px;
  font-weight: 700;
  line-height: 1.11;
}

.nav-links-contacts-email {
  color: var(--main-p-color);
  letter-spacing: .02em;
  font-size: 20px;
  font-weight: 500;
  line-height: 1.2;
}

.active {
  color: var(--main-nav-color);
}

@media only screen and (min-width: 768px) {
  .navigation {
    display: flex;
  }
}

.logo {
  margin-right: 76px;
  text-decoration: none;
}

.current {
  color: var(--main-hover-color);
  position: relative;
}

.header-btn {
  width: 200px;
  height: 50px;
  color: #fff;
  cursor: pointer;
  background-color: #404bbf;
  border: none;
  border-radius: 10px;
  font-size: 16px;
}

.header-btn:hover {
  color: #00f;
}

.video {
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -100;
  background-position: 50%;
  background-size: cover;
  margin: auto;
  position: absolute;
  top: -100%;
  bottom: -100%;
  left: -100%;
  right: -100%;
}

.hero-text {
  color: orange;
  z-index: 1;
  font-size: 4rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.about {
  margin-top: 100vh;
}



