.video {
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -100;
  background-position: 50%;
  background-size: cover;
  margin: auto;
  position: absolute;
  top: -100%;
  bottom: -100%;
  left: -100%;
  right: -100%;
}
.hero-text {
  position: absolute;
  top: 50%;
  left: 50%;
  color: orange;
  transform: translate(-50%, -50%);
  z-index: 1;
  font-size: 4rem;
}